import dateUtil from "../../common/dateUtil.js"

var isLoadInterFace = false;
var _self;
export default {
	data() {
		return {
			listIsFree:false, 
			storeId:0,
			loginInfo:{},
			currentPage:1,
			totalPage:1,
			isLast:true,
			dataList:[],
			dataInfo:{}
		}
	},
	onReachBottom() {
		this.nextPage();
	},
	onLoad(opt) {
		_self=this;
		
		var loginRes = this.checkLogin('../index/personal', '2');
		if(!loginRes){return false;}
		this.loginInfo = loginRes;
		
		if(opt.id != undefined){
			console.log(opt.id);
			_self.storeId = opt.id;
		}
	},
	onShow() {
		_self = this;
		if(_self.storeId != 0){
			_self.dataList = [];
			_self.getDataList(1);
		}
	},
	methods: {
		//修改活动状态
		changeStatus(i,id){
			console.log(i,id,_self.loginInfo.uid);
			uni.request({
			    url: _self.$httpUrl+'index.php/api/store/upstatus', 
				method:"POST",
				data:{
					coupon_id:id,
					status:i
				},
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					_self.dataList = [];
					_self.getDataList(1);
				  }else{
					  uni.showToast({
					  	icon:"none",
					  	title:res.data.msg
					  })
				  }

			    },fail: (e) => {
					
			    },complete: (e) => {
			    }
			});
			
		},
		
		//发布活动
		addActivity(){
			uni.navigateTo({
				url:"create_activity?id="+_self.storeId
			})
		},
		//去领取详情页
		goDetail(){
			uni.navigateTo({
				url:"get_info?id="+_self.storeId
			})
		},
		//加载下一页
		nextPage(){
			console.log(_self.isLast);
			if(_self.isLast){
				uni.showToast({
					title: '没有更多了',
					icon:"none"
				});
			}else{
				if(!isLoadInterFace){
					isLoadInterFace = true;
					var pageNum = _self.currentPage + 1;
					_self.getDataList(pageNum);
				}
			}
		},
		getDataList(pageNum){
			var dataObj = {
					storeid:_self.storeId,
					limit:10,
					page:pageNum
				};
			
			console.log(dataObj);
			uni.request({
			    url: _self.$httpUrl+'index.php/api/store/getCoupon', 
				method:"POST",
				data:dataObj,
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					_self.dataInfo = res.data.result;
					_self.dataList = _self.dataList.concat(res.data.result.couponlist.data);
					_self.currentPage = res.data.result.couponlist.current_page;
					_self.isLast = _self.currentPage >= res.data.result.couponlist.last_page ? true : false;
				  }else{
					  uni.showToast({
					  	icon:"none",
					  	title:res.data.msg
					  })
				  }
				  
				  if(_self.dataList.length <= 0){
				  	_self.listIsFree = true;
				  }else{
				  	_self.listIsFree = false;
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					isLoadInterFace = false;
			    }
			});
		}
	}
}

